/*自定义页面的背景色*/
body {
  background-color: #EDECF3; }

/*页面的安全宽度，如果是使用固定定位fixed的就需要添加.container*/
.container {
  max-width: 480px;
  min-width: 320px;
  margin: 0 auto; }

/*当页面有底部导航时在外层元素加这个class保证内容不被底部导航覆盖*/
.jin-wrap {
  padding-bottom: 56px; }

/*行内元素的垂直居中处理，包括图片下方有空白间隙也可以解决*/
.ver-middle {
  vertical-align: middle; }

/*左右浮动*/
.pull-right {
  float: right !important; }

.pull-left {
  float: left !important; }

/*清除浮动*/
.clearfix:before,
.clearfix:after {
  display: table;
  content: " "; }

.clearfix:after {
  clear: both; }

