/*自定义按钮背景色*/
.jin-btn-yellow {
  background: #FFCB00; }

.jin-btn-yellow:active {
  box-shadow: inset 0px 100px 0px rgba(0, 0, 0, 0.1); }

/*相对定位*/
.rlt {
  position: relative; }

/*绝对定位*/
.abs {
  position: absolute; }

/*两端对齐居中*/
.jin-justify-flex {
  width: 100%;
  display: -webkit-box;
  -webkit-box-pack: justify;
  -webkit-box-align: center; }

/*有时候在用框架的ui-borderd有问题时用自定义的jin-border*/
.jin-border {
  border: 1px solid #e0e0e0; }

/*重置框架头像的默认背景图*/
.ui-avatar,
.ui-avatar-s,
.ui-avatar-lg {
  background-image: none; }

/*自定义列表的样式，用时和框架的ui-list结合使用*/
.jin-list > li {
  padding-right: 15px;
  padding-left: 15px;
  margin-left: 0;
  -webkit-box-align: center; }

@media (max-width: 320px) {
  .jin-list > li {
    padding-right: 10px;
    padding-left: 10px; } }

/*自定义列表选项的样式，用时和框架的ui-list结合使用*/
.jin-list-link > li {
  padding-right: 30px;
  -webkit-box-align: center; }

/*自定义列表箭头和带箭头的表单选项*/
.jin-list-link > li:after,
.jin-form-item-link:after {
  font-family: "jin-iconfont" !important;
  font-size: 20px;
  line-height: 44px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: .2px;
  display: block;
  color: rgba(0, 0, 0, 0.5);
  color: #c7c7c7;
  content: "\e612";
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -21px;
  margin-right: -10px; }

/*flex-box左右和垂直居中,高度需要自己定义*/
.jin-box-center {
  display: -webkit-box;
  width: 100%;
  -webkit-box-flex: 1;
  -webkit-box-sizing: border-box;
  -webkit-box-orient: vertical;
  -webkit-box-pack: center;
  -webkit-box-align: center; }

/*flex-box左右居中,高度需要自己定义*/
.jin-box-pack {
  display: -webkit-box;
  width: 100%;
  -webkit-box-flex: 1;
  -webkit-box-sizing: border-box;
  -webkit-box-pack: center; }

/*flex-box垂直居中,高度需要自己定义*/
.jin-box-align {
  display: -webkit-box;
  width: 100%;
  -webkit-box-flex: 1;
  -webkit-box-sizing: border-box;
  -webkit-box-align: center; }

/******添加链接箭头******/
.jin-link:after {
  font-family: "jin-iconfont" !important;
  font-size: 20px;
  line-height: 44px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: .2px;
  display: block;
  color: rgba(0, 0, 0, 0.5);
  color: #c7c7c7;
  content: "\e612";
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -22px;
  margin-right: -10px; }

/*不可用时的样式，如果不是在button或表单元素上，那仅仅是样式改变不是真正的不可用，禁用需要自己用js控制*/
[disabled]
.disabled {
  color: #fff;
  background: #e9ebec;
  -webkit-background-clip: padding-box;
  background-clip: padding-box; }

/*自定义文字截断行数 ，截取两行或单行用框架定义好的*/
.line-clamp {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical; }

.line-clamp-3 {
  -webkit-line-clamp: 3; }

.line-clamp-4 {
  -webkit-line-clamp: 4; }
